.text-center {
  text-align: center;
}

.seccion {
  margin-top: 5rem/2;
  margin-bottom: 5rem/2;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.contenido-centrado {
  margin: auto;
  max-width: 60rem;
}

.shadow {
  --tw-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1),0 1px 2px 0 rgba(0, 0, 0, 0.06);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.2;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
  border-radius: 0.5rem;
  padding: 0.5rem;
}

.seccion {
  margin-top: 5rem;
  margin-bottom: 5rem;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}
img .imagen-small,
picture .imagen-small {
  max-width: 30rem;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 5rem;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.boton-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  text-decoration: none;
  text-align: center;
  margin-top: separacion/2;
  display: block;
  border: none;
  padding: 1.1rem 1.3rem 1.1rem 1.3rem;
  border-radius: 0.5rem;
  font-weight: 700;
  box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.62);
  -webkit-box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.62);
  -moz-box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.62);
  margin-top: 1rem;
  margin-bottom: 1rem;
}
.boton-amarillo-block:hover {
  background-color: #c77808;
  transition: all 1s;
  cursor: pointer;
}

