  text-decoration: none;
  text-align: center;
  margin-top: separacion/2;
  display: inline-block;
  border: none;
  padding: 1.1rem 1.3rem 1.1rem 1.3rem;
  border-radius: 0.5rem;
  font-weight: 700;
  box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.62);
