  -moz-box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.62);
  margin-top: 1rem;
  margin-bottom: 1rem;
}
.boton-amarillo:hover {
  background-color: #c77808;
  transition: all 1s;
  cursor: pointer;
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  text-decoration: none;
  text-align: center;
  margin-top: separacion/2;
  display: inline-block;
  border: none;
  padding: 1.1rem 1.3rem 1.1rem 1.3rem;
  border-radius: 0.5rem;
  font-weight: 700;
  box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.62);
  -webkit-box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.62);
  -moz-box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.62);
  margin-top: 1rem;
  margin-bottom: 1rem;
  margin-top: 2rem;
}
.boton-verde:hover {
  background-color: #619800;
  transition: all 1s;
  cursor: pointer;
}

.boton-rojo-block {
  background-color: red;
  color: #FFFFFF;
  text-decoration: none;
  text-align: center;
  margin-top: separacion/2;
  display: block;
  border: none;
  padding: 1.1rem 1.3rem 1.1rem 1.3rem;
  border-radius: 0.5rem;
  font-weight: 700;
  box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.62);
  -webkit-box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.62);
