  margin-top: 1rem;
  margin-bottom: 1rem;
  width: 100%;
}
.boton-rojo-block:hover {
  background-color: #e60000;
  transition: all 1s;
  cursor: pointer;
}

.boton-verde-block {
  background-color: #71B100;
