.boton-verde-block:hover {
  background-color: #619800;
  transition: all 1s;
  cursor: pointer;
}

.no-webp .inicio {
  background-image: url("../img/header.jpg");
}

.webp .inicio {
  background-image: url("../img/header.webp");
}

header {
  min-height: 70rem;
  flex-direction: column;
}
header .contenido_header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0;
  max-width: 50rem;
  transform: translateY(45vh);
}
header .contenido_header h1, header .contenido_header h2 {
  padding-left: 3rem;
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 3rem;
  margin: 0;
}

.simpleheader {
  min-height: initial;
}

.titulo {
  color: #FFFFFF;
}

nav {
  display: flex;
  justify-content: space-around;
}
@media (max-width: 768px) {
  nav {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0;
  }
}
nav .logo {
  margin: 0;
  text-transform: uppercase;
  cursor: pointer;
}
nav .logo a {
  text-decoration: none;
}
nav .logo p {
  color: #FFFFFF;
  font-weight: 300;
  font-size: 4.5rem;
  margin: 0;
}
nav .logo span {
  color: #FFFFFF;
  font-weight: 700;
}
nav ul {
  margin: 0;
  display: flex;
  padding: 1rem;
  justify-content: center;
  align-items: center;
}
nav ul li {
  margin: 1rem;
  list-style: none;
}
nav ul li a {
  text-decoration: none;
  color: #FFFFFF;
}
nav ul li a:hover {
  color: #71B100;
}

.dark {
  background-color: #000000;
  margin-left: 0;
  margin-right: 0;
  justify-content: space-around;
}

.dark-mode-button {
  width: 4.5rem;
  margin-left: 1rem;
  filter: invert(100%);
}

.mobile-menu {
  display: none;
  max-width: 4rem;
}

@media (max-width: 768px) {
  nav .mobile-menu {
    display: initial;
    cursor: pointer;
  }
  nav ul {
    position: fixed;
    display: flex;
    flex-direction: column;
    transition: all 0.5s;
    transform: translateY(-40rem);
  }
}

.on {
  transition: all 0.5s;
  transform: translateY(20rem);
  background-color: rgba(44, 43, 43, 0.644);
  border-radius: 2rem;
}

footer {
  background-color: #333333;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5rem;
}
footer p {
  margin-bottom: 3rem;
  color: #FFFFFF;
}
@media (max-width: 480px) {
  footer ul {
    display: none;
  }
}

.cards {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(3, 1fr);
}
@media (max-width: 768px) {
  .cards {
    display: flex;
    flex-direction: column;
  }
}

.card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}
.card h3 {
  margin: 1rem;
}

.icono img {
  height: 10rem;
}

.anuncio {
  display: flex;
  flex-direction: column;
  width: 40rem;
  border: none;
  border-radius: 1rem;
  background-color: #FFFFFF;
}
.anuncio picture img {
  width: 40rem;
  height: 30rem;
  object-fit: cover;
  border: none;
  border-radius: 1rem;
}
@media (max-width: 480px) {
  .anuncio picture img {
    object-fit: cover;
  }
}

.contenido__anuncio {
  display: grid;
  padding: 1rem;
  height: auto;
}
.contenido__anuncio h3 {
  padding: 1rem;
  margin: 1rem;
  font-weight: 400;
}
.contenido__anuncio p {
  margin: 0;
  font-weight: 300;
}
.contenido__anuncio .descripcion {
  text-align: center;
  min-width: auto;
  min-height: 252px;
  max-height: 252px;
  overflow: scroll;
  margin: 1rem;
}
@media (max-width: 480px) {
  .contenido__anuncio .descripcion {
    text-align: center;
    min-width: auto;
    min-height: unset;
    max-height: unset;
    margin: 1rem;
  }
}
.contenido__anuncio .precio {
  color: #71B100;
  font-size: 3rem;
}

.precio {
  color: #71B100;
  font-size: 3rem;
}

.iconos__caracteristicas {
  list-style: none;
  display: flex;
  justify-content: space-around;
  padding: 0;
  font-weight: 700;
}
.iconos__caracteristicas li {
  display: flex;
  align-items: center;
  flex-direction: row;
  gap: 1rem;
}
.iconos__caracteristicas li img {
  max-width: 3.5rem;
}

.contenedor__anuncios {
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(3, 1fr);
}
@media (max-width: 480px) {
  .contenedor__anuncios {
    display: flex;
    flex-direction: column;
  }
}
@media (max-width: 768px) {
  .contenedor__anuncios h3 {
    margin: 0;
  }
}

.no-webp .contacto {
  background-image: url("../img/encuentra.jpg");
}

.webp .contacto {
  background-image: url("../img/encuentra.webp");
}

.contacto {
  background-position: center center;
  background-size: cover;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contacto h2 {
  color: #FFFFFF;
  font-weight: 700;
}
.contacto p {
  color: #FFFFFF;
}

.contacto-contenedor form fieldset {
  display: flex;
  flex-direction: column;
}
.contacto-contenedor form fieldset label, .contacto-contenedor form fieldset p {
  font-weight: 700;
}
.contacto-contenedor form fieldset input {
  margin-bottom: 2rem;
}

.formulario fieldset {
  display: flex;
  flex-direction: column;
}
.formulario fieldset label, .formulario fieldset p {
  font-weight: 700;
}
.formulario fieldset input {
  margin-bottom: 2rem;
}

.seccion-inferior {
  display: grid;
  grid-template-columns: 2fr 1fr;
}
@media (max-width: 768px) {
  .seccion-inferior {
    display: flex;
    flex-direction: column;
  }
}

@media (max-width: 480px) {
  .blog {
    display: flex;
    flex-direction: column;
  }
}

.entrada {
  display: grid;
  grid-template-columns: 1fr 2fr;
  column-gap: 2rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
}
@media (max-width: 480px) {
  .entrada {
    display: flex;
    flex-direction: column;
  }
}
.entrada picture {
  max-width: 80rem;
  cursor: pointer;
}
.entrada .texto-entrada h4, .entrada .texto-entrada p, .entrada .texto-entrada span, .entrada .texto-entrada a {
  margin: 0;
}
.entrada .texto-entrada a {
  text-decoration: none;
  color: #000000;
}
.entrada .texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
}
.entrada .texto-entrada span {
  color: #E08709;
  cursor: pointer;
}

.testimonial {
  background-color: #619800;
  border-radius: 3rem;
  color: #FFFFFF;
  font-size: 2rem;
  padding: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  position: absolute;
  transform: translateX(-8rem);
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  height: 8rem;
  width: 6rem;
  display: block;
}
.testimonial p {
  color: #FFFFFF;
  text-align: right;
  font-style: italic;
}

.contenido-nosotros {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
}
.contenido-nosotros .contenido-nosotros-texto {
  display: flex;
  flex-direction: column;
}
.contenido-nosotros .contenido-nosotros-texto h3 {
  margin: 1rem;
}
@media (max-width: 768px) {
  .contenido-nosotros {
    display: flex;
    flex-direction: column;
  }
}

.entrada-contenido-texto h4, .entrada-contenido-texto p, .entrada-contenido-texto span, .entrada-contenido-texto a {
  margin: 0;
}
.entrada-contenido-texto a {
  text-decoration: none;
  color: #000000;
}
.entrada-contenido-texto span {
  color: #E08709;
  cursor: pointer;
}

.entrada-contenido picture {
  margin: 0 auto;
  margin-bottom: 1rem;
}

.darkmode {
  background-color: #262626;
}
.darkmode h1, .darkmode h2, .darkmode h3, .darkmode h4 {
  color: #FFFFFF;
}
.darkmode .card, .darkmode p {
  color: #FFFFFF;
}
.darkmode .anuncio {
  background-color: #333333;
}
.darkmode .iconos__caracteristicas li img {
  filter: invert(100%);
}
.darkmode form {
  color: #FFFFFF;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  margin-bottom: 1rem;
  border: none;
  padding: 1.3rem 1.5rem 1.3rem 1.5rem;
  border-radius: 0.5rem;
  font-weight: 700;
  box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.75);
}
.alerta.error {
  background-color: red;
}
.alerta.exito {
  background-color: #1db81d;
}

.tabla-moderna {
  width: 90%;
  border-radius: 1rem;
  margin: 0 auto;
  background-color: #f9fafb;
}
.tabla-moderna .tablita {
  width: 100%;
}
.tabla-moderna .tablita thead {
  background-color: #f9fafb;
  line-height: 3.4rem;
}
.tabla-moderna .tablita thead th {
  color: #6b7280;
}
.tabla-moderna .tablita tbody tr {
  background-color: #FFFFFF;
  border: none;
}
.tabla-moderna .tablita tbody .imagen-tabla {
  width: 15rem;
}

.image_tiny {
  width: 20rem;
}

.contenedor-formulario {
  width: 100%;
}

.formulario-moderno {
  border-radius: 1.5rem;
  background-color: white;
